.App {
    text-align: center;
    width: 100vw;
    height: 100vh;
    text-align: left;
    color: rgb(37, 37, 37);
}

.title-container {
    width: 100%;
}

.title-input {
    font-size: 50px;
    width: 100%;
    padding: 0px 5px;
}

.content-container {
    margin: 35px 0;
}

.content-input {
    outline: none;
    padding: 5px 10px;
    font-size: 18px;
}
.author-input,
.phone-input {
    font-size: 18px;
    padding: 0 10px;
    width: 100%;
    margin: 15px 0;
}
.agree-container,
.button-container {
    padding: 0 10px;
    margin: 15px 0;
}

button {
    background-color: white;
    color: rgb(30, 30, 30);
    font-size: 18px;
    font-family: var(--font-NotoSerif);
    border: 1px solid rgb(183, 183, 183);
    padding: 5px 15px;
}
hr {
    border: 0.5px solid rgb(248, 248, 248);
    margin: 10px 0;
}
button:hover {
    background-color: rgb(245, 245, 245);
    cursor: pointer;
}

button:disabled {
    background-color: rgb(245, 245, 245);
    color: rgb(183, 183, 183);
    cursor: not-allowed;
}
input {
    border: none;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;

    resize: none; /*remove the resize handle on the bottom right*/
    font-family: var(--font-NotoSerif);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type='number'] {
    -moz-appearance: textfield;
}

[placeholder]:empty::before {
    content: attr(placeholder);
    color: #555;
}

[placeholder]:empty:focus::before {
    content: '';
}

.error {
    font-size: 13px;
    padding: 5px 10px;
}

.menu-bar {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
}

.menu-bar img {
    width: 100%;
}

.front-image {
    /* fix image to be centered all the time */
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    width: 250px;
    height: auto;
    text-align: center;
}

.front-image img {
    width: 100%;
}

.buttons {
    /* display item on right */
    position: fixed;
    top: 50px;
    right: 80px;
}

.image-button {
    margin: 30px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.image-button-image {
    width: 80px;
    height: auto;
}

.image-button:hover {
    cursor: pointer;
}

.button-name {
    font-size: 18px;
    font-family: var(--font-Apple);
    margin: 5px 0;
    text-align: center;
    text-shadow: 1px 1px 5px rgba(136, 136, 136, 0.361);
}

.quotation {
    border-left: 3px solid rgb(183, 183, 183);
    margin: 10px 0;
}

.no-deco-quotation {
    margin: 10px 0;
    font-style: italic;
}
@media screen and (max-width: 500px) {
    .menu-bar {
        width: 360%;
        height: 60px;
    }

    .buttons {
        /* display item on right */
        top: 35px;
        right: 40px;
    }

    .App {
        width: 95%;
    }

    .title-input {
        font-size: 30px;
    }

    .content-input,
    .author-input,
    .phone-input {
        font-size: 14px;
    }

    button {
        font-size: 15px;
        padding: 5px 12px;
    }

    .image-button-image {
        width: 60px;
        height: auto;
    }

    .button-name {
        font-size: 15px;
        font-family: var(--font-Apple);
        margin: 5px 0;
        text-align: center;
        text-shadow: 1px 1px 5px rgba(136, 136, 136, 0.361);
    }

    .front-image {
        /* fix image to be centered all the time */

        width: 200px;
        height: auto;
    }
    .front-image img {
        width: 100%;
    }
}

/* table view */
@media screen and (min-width: 501px) and (max-width: 1280px) {
    .menu-bar {
        width: 180%;
        height: 60px;
    }

    .buttons {
        /* display item on right */
        right: 40px;
    }
}
