.modal {
    border-radius: 20px;
    width: 50%;
    height: 600px;
    background-color: white;
    margin-top: 50px;
    border: 0.5px solid #dedede;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 12px;
    position: fixed;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.modal-header {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
}

.modal-header img {
    width: 100%;
}

.modal-header:hover {
    cursor: pointer;
}

.modal-content {
    overflow-y: scroll;
}

@media screen and (max-width: 768px) {
    .modal {
        width: 90%;
        height: 500px;
    }
}

/* table view */
@media screen and (min-width: 769px) and (max-width: 1280px) {
    .menu-bar {
        width: 300%;
        height: 60px;
    }

    .buttons {
        /* display item on right */
        right: 40px;
    }
}
